import styles from "./Sidebar.module.css";
import React, { Children, useState } from "react";

const DrawerToggle = ({ name, isOpen, onToggle }) => {
  return (
    <button
      className={`${styles.drawerToggle} ${isOpen ? styles.open : ""}`}
      onClick={() => onToggle(name)}
    >
      <span className={styles.toggleLabel}>{name}</span>
    </button>
  );
};

export const Sidebar = ({ children }) => {
  const [activeDrawer, setActiveDrawer] = useState(null);

  const toggleDrawer = (drawerName) => {
    setActiveDrawer((prev) => (prev === drawerName ? null : drawerName));
  };

  const drawerNames = Children.toArray(children)
    .filter((child) => child.type.name === "Drawer")
    .map((child) => ({
      name: child.props.name,
    }));

  return (
    <div className={styles.sidebarContainer}>
      <div className={styles.sidebar}>
        {drawerNames.map(({ name }) => (
          <DrawerToggle
            name={name}
            isOpen={activeDrawer === name}
            onToggle={toggleDrawer}
          />
        ))}
      </div>
      <div
        className={`${styles.drawerContainer} ${activeDrawer ? styles.open : ""}`}
      >
        {React.Children.map(children, (child) =>
          React.cloneElement(child, {
            isOpen: child.props.name === activeDrawer,
          })
        )}
      </div>
    </div>
  );
};
